import React, { createContext, useContext, useState, ReactNode } from 'react';

// Available languages
export type Language = 'en' | 'fr' | 'es' | 'ar';

// Translation interface
export interface Translations {
  [key: string]: {
    en: string;
    fr: string;
    es: string;
    ar: string;
  };
}

// Context props
interface LanguageContextProps {
  language: Language;
  setLanguage: (lang: Language) => void;
  t: (key: string) => string;
}

// Create context
const LanguageContext = createContext<LanguageContextProps | undefined>(undefined);

// Translations data
const translations: Translations = {
  "home": {
    en: "Home",
    fr: "Accueil",
    es: "Inicio",
    ar: ""
  },
  "cars": {
    en: "Cars",
    fr: "Voitures",
    es: "Coches",
    ar: ""
  },
  "dakhla": {
    en: "Dakhla",
    fr: "Dakhla",
    es: "Dakhla",
    ar: ""
  },
  "about": {
    en: "About",
    fr: " propos",
    es: "Acerca de",
    ar: " "
  },
  "contact": {
    en: "Contact",
    fr: "Contact",
    es: "Contacto",
    ar: " "
  },
  "book_now": {
    en: "Book Now",
    fr: "Rserver",
    es: "Reservar",
    ar: " "
  },
  "discover_dakhla": {
    en: "Discover Dakhla",
    fr: "Dcouvrez Dakhla",
    es: "Descubre Dakhla",
    ar: " "
  },
  "explore_beauty": {
    en: "Explore the beauty of Dakhla with our premium car rental service",
    fr: "Explorez la beaut de Dakhla avec notre service de location de voitures premium",
    es: "Explora la belleza de Dakhla con nuestro servicio premium de alquiler de coches",
    ar: "        "
  },
  "book_car_now": {
    en: "Book a Car Now",
    fr: "Rserver une voiture",
    es: "Reservar un coche",
    ar: "  "
  },
  "explore_fleet": {
    en: "Explore Our Fleet",
    fr: "Dcouvrez notre flotte",
    es: "Explora nuestra flota",
    ar: " "
  }
};

export const LanguageProvider = ({ children }: { children: ReactNode }) => {
  const [language, setLanguage] = useState<Language>('en');

  // Translation function
  const t = (key: string): string => {
    return translations[key]?.[language] || key;
  };

  return (
    <LanguageContext.Provider value={{ language, setLanguage, t }}>
      {children}
    </LanguageContext.Provider>
  );
};

export const useLanguage = () => {
  const context = useContext(LanguageContext);
  if (context === undefined) {
    throw new Error('useLanguage must be used within a LanguageProvider');
  }
  return context;
};
